  max-width: 100%;
  text-align: right;
  padding: 0 15px;
}

/*-- contact section ends --*/
.style-switcher {
  position: fixed;
  right: 0;
  top: 77px;
  padding: 15px;
  width: 200px;
  z-index: 101;
  border-radius: 5px;
  transition: all 0.3s ease;
  transform: translateX(100%);
}

.style-switcher.open {
  transform: translateX(-15px);
}

.style-switcher .s-icon {
  position: absolute;
  height: 40px;
  width: 40px;
  text-align: center;
  font-size: 20px;
  color: var(--text-black-900);
  right: 100%;
  margin-right: 15px;
  cursor: pointer;
  transition: all 0.3s ease;
  border-radius: 50%;
}

.style-switcher .s-icon i {
  line-height: 40px;
}

.style-switcher .s-icon:after {
  border-radius: 50%;
}

.style-switcher .style-switcher-toggler {
  top: 0;
}

.style-switcher .day-night {
  top: 55px;
}

.style-switcher h4 {
  margin: 0 0 10px;
  color: var(--text-black-700);
  font-style: 16px;
  font-weight: 600;
  text-transform: capitalize;
}

.style-switcher .colors {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.style-switcher .colors span {
  display: inline-block;
  height: 30px;
  width: 30px;
  border-radius: 50%;
  cursor: pointer;
}

.style-switcher .color-1 {
  background-color: #BF40BF;
}

.style-switcher .color-2 {
  background-color: #ec9412;
}

.style-switcher .color-3 {
  background-color: #1fc586;
}

.style-switcher .color-4 {
  background-color: #2eb1ed;
}

.style-switcher .color-5 {
  background-color: #cc3a3b;
}

/*-------------------------- Mobile Responsive ------------------------*/
@media (max-width: 767px) {
  .home-section .home-text, .home-section .home-img, .about-section .about-img, .about-section .about-info, .about-section .skills .skill-item, .service-section .service-item, .portfolio-section .portfolio-item, .portfolio-popup .description, .portfolio-popup .about-info, .contact-section .contact-item, .contact-section .w-50 {
    flex: 0 0 100%;
    max-width: 100%;
  }

  .home-section .home-text {
    order: 2;
    text-align: center;
  }

  .home-section .full-screen {
    padding: 100px 0;
  }

  .home-section .home-img .img-box {
    margin-bottom: 40px;
    max-width: 350px;
  }

  .contact-section .submit-btn, .about-info a {
    text-align: center;
    justify-content: center;
    display: flex;
    align-items: center;
  }

  .effect-3 {
    margin-left: -60px;
  }

  .effect-4 {
    left: 15%;
  }

  .effect-5 {
    right: 4%;
  }

  .about-section .about-img {
    padding-bottom: 30px;
  }

  .about-section .timeline .timeline-item:nth-child(odd) {
    padding-right: 15px;
    text-align: left;
    padding-left: 75px;
  }

  .about-section .timeline .timeline-item:nth-child(even) {
    padding-left: 75px;
    padding-right: 15px;
  }

  .about-section .timeline:before {
    left: 35px;
  }

  .about-section .timeline .timeline-item:nth-child(odd) .timeline-item-inner::before {
    left: -30px;
    right: auto;
  }

  .about-section .timeline .timeline-item:nth-child(odd) .icon {
    right: auto;
    left: -60px;
  }

  .about-section .timeline .timeline-item:nth-child(even) .icon {
    left: -60px;
  }

  .about-section .timeline .timeline-item {
    margin-bottom: 40px;
  }

  .about-section .timeline .timeline-item:last-child {
    margin-bottom: 0;
  }
}
@media (max-width: 574px) {
  .home-section .home-img .img-box {
    max-width: 260px;
  }

  .home-section .home-text h2 {
    font-size: 40px;
  }
}
@media (max-width: 991px) {
  .service-section .service-item, .portfolio-section .portfolio-item {
    flex: 0 0 50%;
    max-width: 50%;
  }
}
